/**
 * 
 */
import {MyChildApp} from '$ui'
import {
  Vue,
  Vuex,
  Router,
  progress,
  Access
} from './enhance'

import uiConfig from '$ui/config'

export {
  Vue,
  Vuex,
  Router,
  progress,
  Access
}

/**
 * views
 * @function getView
 * @param {string} path vueviews
 * @return {Promise} promise
 *
 * @example
 * //  /apps/app1/views/index.vue 
 *  getView('index')
 */
export const getView = require('$ui/import/app/' + process.env.NODE_ENV)

/**
 * 
 * @type {VueRouter}
 */
let router = null

/**
 * 
 */
let instance = null

/**
 * 
 */
let access = null

/**
 * 
 * @return {Promise<void>}
 */
async function bootstrap() {
  progress.done()
}

/**
 * 
 * @return {Promise<void>}
 */
async function unmount() {
  if (!instance) return
  instance.$destroy();
  // 
  instance.$el.innerHTML = '';
  instance = null
  router = null
  access = null
}


/**
 * path
 * @param {Array} routes
 * @param {string} prefix
 * @param {Object} parent 
 */
export function inject(routes = [], prefix = '', parent = null) {
  if (!prefix) return
  routes.forEach(route => {
    const path = route.path
    if (path.startsWith('/') || path === '') {
      route.path = `${prefix}${route.path}`
    } else {
      route.path = parent ? parent.path : `${prefix}`
    }
    if (route.children) {
      inject(route.children, prefix, route)
    }
  })
}

/**
 * 
 * @param {object} opts 
 * @param {string} opts.el 
 * @param {Array} opts.routes 
 * @param {Object} opts.config 
 * @param {Vuex} store vuex
 * @return {*}
 */
export function createMicroApp({el, routes, config, store}) {
  if (config.routePrefix) {
    inject(routes, config.routePrefix)
  }
  return {
    /**
     * 
     * @return {Promise<void>}
     */
    async mount(props) {
      router = new Router({
        routes,
        ...config.router
      })
      access = new Access(Vue, {
        ...config.access,
        $router: router,
        progress
      })
      instance = new Vue({
        components: {
          MyChildApp
        },
        provide() {
          return {
            appProps: props
          }
        },
        router,
        store,
        access,
        render: h => h('my-child-app', {
          props: uiConfig.scale
        })
      }).$mount(el)
    },
    async update(props) {
      console.log('update props', props);
    },
    bootstrap,
    unmount
  }
}





