/**
 *  .my/core/build/routes-builder-plugin.js webpack
 */
export default function ({
  get
}) {
  return [{
    path: '/',
    component: get('_layout'),
    children: [{
      path: '',
      component: get('index'),
      meta: {
        'title': 'ElementUI'
      },
      props: true
    }, {
      path: '/pro',
      component: get('pro'),
      meta: null,
      props: true
    }, {
      path: '/ui',
      component: get('ui'),
      meta: {
        'title': ''
      },
      props: true
    }],
    meta: {
      'title': ''
    },
    props: true
  }, {
    path: '/*',
    component: get('_404'),
    meta: null,
    props: true
  }]
}
